.about {
    padding: 50px 0;
    background-color: #f4f4f4;
    color: #333;
    text-align: center;
  }
  
  .about .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
  }
  
  .about h2 {
    font-size: 2.5rem;
    margin-bottom: 20px;
    color: #2c3e50;
  }
  
  .about-content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .about p {
    font-size: 1rem;
    line-height: 1.6;
    margin-bottom: 20px;
  }
  
  .about-details {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }
  
  .detail {
    background: #fff;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    width: 300px;
  }
  
  .detail h3 {
    font-size: 1.5rem;
    margin-bottom: 10px;
    color: #3498db;
  }
  
  .detail ul {
    list-style: none;
    padding: 0;
  }
  
  .detail li {
    margin-bottom: 10px;
  }
  
  .detail strong {
    color: #2c3e50;
  }
  
  @media (max-width: 768px) {
    .about-details {
      flex-direction: column;
      align-items: center;
    }
  
    .detail {
      width: 90%;
      margin-bottom: 20px;
    }
  }
  